import React from "react";

const AddItem = ({ add }) => {
  const [value, setValue] = React.useState("");
  const handleSubmit = (e) => {
    e.preventDefault();

    if (!value) return;
    add({ text: value, isDone: false });
    setValue("");
  };
  //Below are react bootstrap
  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        className="input"
        size="sm"
        value={value}
        onChange={(e) => setValue(e.target.value)}
        placeholder="Add new to Do"
      ></input>

      <button variant="primary" type="submit">
        Submit
      </button>
    </form>
  );
};

const DisplayItemList = ({ item, ind, markCross }) => {
  console.log(item, "total");
  return (
    <ul>
      <li
        style={{ textDecoration: item.isDone ? "line-through" : "" }}
        onClick={() => markCross(ind)}
      >
        {item.text}
      </li>
    </ul>
  );
};

const TodoList = () => {
  const [toDo, settoDo] = React.useState([
    {
      text: "Sample to Do",
      isDone: false
    }
  ]);
  const [value, setvalue] = React.useState(toDo.length);
  const [total, setTotal] = React.useState(toDo.length);
  const add = (text) => {
    const newtoDo = [...toDo, text];
    settoDo(newtoDo);
    setTotal(newtoDo.length);
    const value = newtoDo && newtoDo.filter((i) => i.isDone === false);
    setvalue(value.length);
  };

  const markCross = (ind) => {
    const newtoDo = [...toDo];
    newtoDo[ind].isDone = true;
    const value = toDo && toDo.filter((i) => i.isDone === false);
    setvalue(value.length);
    settoDo(newtoDo);
    setTotal(toDo.length);
  };

  return (
    <div className="container">
      <h1 className="text-center">To Do List</h1>
      {<AddItem add={add} />}
      <div>
        <div>{value + "  remaining out of " + total + " tasks"}</div>
        {toDo &&
          toDo.map((item, ind) => (
            <div>
              <DisplayItemList item={item} ind={ind} markCross={markCross} />
            </div>
          ))}
      </div>
    </div>
  );
};
export default TodoList;
